.containerHome2{
    display: flex;
    justify-content: center;
    height: 25rem;
    padding: 1rem;
}

.textdiv{
    width: 80vw;
    height: 20rem;
    display: flex;
    flex-direction: column;
    align-items: center;

}
.h2{
    font-family: 'Lato', sans-serif;
    color: #094183;
}
.p{
    font-family: 'Lato', sans-serif;
    color: #094183;
    font-size: 1.1rem;
}

@media (max-width:836px) {
    .h2{
        font-size: 1.4rem;
    }
    .p{
        font-size: 1rem;
    }
}
@media (max-width:686px) {
    .h2{
        font-size: 1.1rem;
    }
    .p{
        font-size:0.8rem;
    }
}
@media (max-width:450px) {

    .h2{
        font-size: 1rem;
    }
    .p{
        font-size:0.7rem;
    }
}