.menuContainer{
    height: 1rem;
    width: 5rem;
    position: absolute;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.subContainer{
    display: flex;
    align-items: center;
    height: 1rem;
    position: relative;
    right: 1rem;
    font-size: 1.4rem;
    color: white;
}

.H4{
    margin-left: 0.5rem;
}
.H4:hover{
    color: #87b2f1;
    transition: 0.4s;
}

.a{
    text-decoration: none;
}

.li{
    list-style-type: none;
}

@media (max-width:1100px) {
    .menuContainer{
        position: absolute;
        bottom: 13px;
    }
    .subContainer{
        font-size: 1.2rem;
    }
}
@media (max-width:936px) {
    .menuContainer{
        position: absolute;
        right: 0.6rem;
        bottom: 0.8rem;
        width: 3rem;
    }
    .H4{
        margin-left: 0rem;
        font-size: 1rem;
    }
}

@media (max-width:817px) {
    .menuContainer{
        position: absolute;
        bottom: 0.6rem;
    }
    .subContainer{
        font-size: 1.1rem;
    }
}

@media (max-width:665px) {
    .H4{
        margin-left: 0rem;
        font-size: 0.8rem;
    }
    .menuContainer{
        position: absolute;
        width: 2.3rem;
        bottom: 0.4rem;
    }
}
