.footerDiv{
    height: 4rem;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    padding: 1.7rem;
    background-color: #09274b; 
}
#para{
    display: flex;
}
#para a{
   padding: 1rem;
   color: #d1d1d1;
   text-decoration: none;
}
#para a:hover{
    color: rgb(158, 157, 157);
}
#para p{
    color: #d1d1d1;

}

@media (max-width:836px) {
    #para{
        font-size: 0.7rem;
    }
}
@media (max-width:686px) {
    #para{
        font-size: 0.6rem;
    }
}