.adminHomeContainer{
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: rgb(228, 230, 235);
    padding: 17px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;


    .allStudent{
        display: flex;
        align-items: center;
        margin-bottom: 8px;
        box-shadow: 0px 15px 10px -15px rgb(191, 192, 194) ;
        cursor: pointer;
        .img{
            width: 6.5rem;
            height: 6.5rem;
            border-radius: 3.5rem;
        }

        .H1{
            padding-left: 7px;
            color: rgb(78, 78, 78);
        }
    }
    .allStudent:hover{
        background-color: rgb(240, 240, 240);
        box-shadow: 2px 2px 2px 2px rgb(212, 213, 216) ;
    }

    .imagesList{
        display: grid;
        // position: sticky;
        // top: 0px;
        padding: 1rem;

        .limg{
            margin: 1rem;
            width: 20rem;
            height: 16.5rem;
            border-radius: 0.7rem;
            box-shadow: 0px 0px 2px 1px #220089 ;
            cursor: pointer;
        }
        .limg2{
            margin: 1rem;
            width: 20rem;
            height: 16.5rem;
            border-radius: 0.7rem;
            box-shadow: 0px 0px 2px 1px #dbdcdf ;
            cursor: pointer;
        }

        .limg:hover{
            box-shadow: 0px 0px 5px 6px rgb(41, 81, 160) ;
        }
        .limg2:hover{
            box-shadow: 0px 0px 5px 6px #c5c5c5 ;
        }
    }
}