.subcontainer1 {
    height: 24.5rem;
    display: flex;
    justify-content: space-between;
    box-shadow: 0px 13px 10px -15px rgb(71, 70, 70);  
}

.imgDiv img{
   height: 24.5rem;
   align-items: flex-end; 
   width: 100%;
}

.textDiv {
    height: 24.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.textDivPH{
    font-family: 'Lato', sans-serif;
    color: #094183;
    position: relative;
    left: 10rem;
}
.textDiv h2{
    font-size: 2rem;
}
.textDiv p{
    font-size: 1.3rem;
    position: relative;
    bottom: 2rem;
}

@media (max-width:1430px) {
    .imgDiv img{
        height: 24.5rem;
        align-items: flex-end; 
     }
     .textDivPH{
        font-family: 'Lato', sans-serif;
        color: #094183;
        position: relative;
        left: 5rem;
    }
}

@media (max-width:1122px) {
    .subcontainer1 {
        height: 15rem;
        display: flex;
        justify-content: space-between;
        box-shadow: 0px 13px 10px -15px rgb(71, 70, 70);  
    }

    .imgDiv img{
        height: 15rem;
        align-items: flex-end; 
     }
     .textDivPH{
        font-family: 'Lato', sans-serif;
        color: #094183;
        position: relative;
        left: 4rem;
        bottom: 3rem;
    }
    .textDiv h2{
        font-size: 1rem;
    }
    .textDiv p{
        font-size: 0.8rem;
        position: relative;
        bottom: 4rem;
    }
}


@media (max-width:888px) {
    .subcontainer1 {
        height: 15rem;
        display: flex;
        justify-content: space-between;
        box-shadow: 0px 13px 10px -15px rgb(71, 70, 70);  
    }
    .imgDiv img{
        height: 15rem;
        align-items: flex-end; 
     }
}

@media (max-width:700px) {
    .subcontainer1 {
        height: 10rem;
        display: flex;
        justify-content: space-between;
        box-shadow: 0px 13px 10px -15px rgb(71, 70, 70);  
    }

    .imgDiv img{
        height: 10rem;
        align-items: flex-end; 
     }
     .textDivPH{
        font-family: 'Lato', sans-serif;
        color: #094183;
        position: relative;
        left: 2rem;
        bottom: 6rem;
    }
    .textDiv h2{
        font-size: 0.9rem;
    }
    .textDiv p{
        font-size: 0.6rem;
        position: relative;
        bottom: 7rem;
    }
}