#navbarBlock{
    height: 5rem;
        background-image: linear-gradient(rgb(175, 175, 175), #dbdbdb);
}

 
#article{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative; 
}


#leftNdiv{
    display: flex;
    justify-content: space-around;
    width: 75%;
}

.midDiv{
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 2rem;
    width: 10rem;
    position: relative;
    right: 2rem;
}

.midNdiv{
  height: 2rem;
  width: 4rem;
  position: absolute;
  top: 1.3rem;
  left: 6.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  border-bottom: 01px solid rgb(53, 52, 52);
  box-shadow: 0px 10px 10px -5px rgb(143, 133, 133) ;
}
.midNdiv:hover{
    border-bottom: 2px solid black;
    transition: 0.1s;
}
.midBdiv{
  height: 2rem;
  width: 5.3rem;
  position: absolute;
  top: 1.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  border-bottom: 01px solid rgb(53, 52, 52);
  box-shadow: 0px 10px 10px -5px rgb(143, 133, 133) ;
}
.midNdiv:hover{
    border-bottom: 2px solid black;
    transition: 0.1s;
}

#linkTag{
   height: 2.5rem;
   text-decoration: none;
   font-size: 18px;
   font-weight: bold;
   color:#888484 ;
   font-family: 'Lato', sans-serif;
   position: relative;
   bottom: 6px;
}
#linkTag:hover{
    color: #21539e;
    transition: 0.3s;
}

#linkTagPara{
    height: 2rem;
}

.textdiv{ 
    position: absolute;
    top: 0;
    left: 7rem;
}
.textdiv p{ 
    font-family: 'Lato', sans-serif;
    font-size: 24px;
    position: relative;
    left: 1rem;
}

.logoBlock  img{
    border-radius: 8px;
    width: 5rem;
    height: 4rem;
    position: absolute;
    top: 5px;
    left: 25px;
}
.menuBlock{
    width: 11rem;
    height: 4.98rem;
    background-color: #21539e;
    border-color: #21539e transparent ;
}

.triangle{
    width: 3rem;
    height: 4.98rem;
    background-color: #21539e;
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%);
}

#spclDesing{
    display: flex;
}

@media (max-width:1100px) {
    #navbarBlock{
        height: 4rem;
    }  
    .textdiv p{ 
        font-size: 19px;
    }
    .logoBlock  img{
        width: 4rem;
        height: 3rem;
        position: absolute;
        top: 5px;
        left: 25px;
    }
    .midNdiv{
        height: 1.5rem;
        width: 4rem;
        position: absolute;
        top: 0.75rem;
     }
    .menuBlock{
        width: 8.5rem;
    }
    .menuBlock{
        height: 4rem;
    }
    .triangle{
        height: 4rem;
    }
}

@media (max-width:936px) {
    .textdiv p{ 
        font-size: 17px;
    }
    #linkTag{
        height: 2rem;
     }
    .midNdiv{
        height: 1.5rem;
        width: 3.5rem;
        position: absolute;
        top: 0.6rem;
    }
    #linkTagPara{
        height: 1.2rem;
        font-size: 15px;
    }
    .menuBlock{
        width: 7rem;
    }
    .menuBlock{
        width: 7rem;
    }
}

@media (max-width:817px) {
    #navbarBlock{
        height: 3.4rem;
    } 
    .textdiv{ 
        position: absolute;
        top: 0;
        left: 3rem;
    }
    .textdiv p{ 
        font-size: 14px;
    }
    .logoBlock  img{
        width: 3rem;
        height: 3rem;
        position: absolute;
        left: 10px;
    }
    .midNdiv{
        height: 1.5rem;
        width: 3.5rem;
        position: absolute;
        top: 0.1rem;
      }
    .menuBlock{
        height: 3.4rem;
    }
    .triangle{
        height: 3.4rem;
    }
    .menuBlock{
        width: 6rem;
    }
}

@media (max-width:665px) {
    .textdiv{ 
        width: 8.5rem;
    }
    .textdiv p{
        position: relative;
        bottom:6px;
        font-size: 12px
    };
    .menuBlock{
        width: 4rem;
    }
    .midDiv{
        height: 1.5rem;
        width: 3.1rem;
        position: absolute;
        left: 12.1rem;
        top: 1.7rem;
        z-index: 10;
    }
    .midNdiv{
        width: 2.6rem;
    }
    #linkTagPara{
        width: 2.6rem;
        background-color: rgb(0, 174, 255);
        color: white;
        border-radius: 3px;
        font-size: 0.7rem;
        text-align: center;
        position: relative;
        top: .2rem;
    }
}